@import url(https://fonts.googleapis.com/css2?family=Raleway:wght@200;300&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

*{
  margin: 0;
  padding:0;
}

body {
  font-family: system-ui;
  font-size: min(200%, 5vmin);
}

h1, p, body {
  margin: 0;
}

p {
  font-family: "Dank Mono", ui-monospace, monospace;
}

/*styles NavBar*/
.navLi{
  cursor: pointer;
  font-family: 'Raleway', sans-serif;
  text-decoration: none;
  color: white;
  justify-content: center;
  width: 145px;
  margin-left: 10px;
  margin-right: 10px;
  display: list-item;
}

.navLi :hover{
  color: #ffc107;
  -webkit-animation-duration: 2000ms;
          animation-duration: 2000ms;
}

.active{
  border-bottom: 2px solid white;
  display: list-item;
  -webkit-animation-duration: 1000ms;
          animation-duration: 1000ms;
}

.mb_nav{
  margin-bottom: 80px;
}
/*styles NavBar End*/

/* Animation */

.animated{
  -webkit-animation-duration: 900ms;
          animation-duration: 900ms;
  -webkit-animation-name: animacionContainer;
          animation-name: animacionContainer;
  scroll-behavior: smooth;
}

@-webkit-keyframes animacionContainer {
  0%{
    margin-bottom: 1000px;
    opacity:0;
    margin-bottom: 0px;
    padding-bottom: 0px;
  }
  100%{
      margin-top: 0px;
      margin-bottom: 0px;
      padding-bottom: 0px;
      opacity:1;
  }
}

@keyframes animacionContainer {
  0%{
    margin-bottom: 1000px;
    opacity:0;
    margin-bottom: 0px;
    padding-bottom: 0px;
  }
  100%{
      margin-top: 0px;
      margin-bottom: 0px;
      padding-bottom: 0px;
      opacity:1;
  }
}
/* Animation End */

